@import url(https://fonts.googleapis.com/css2?family=Comfortaa:wght@400;700&family=Roboto+Mono&display=swap);
#root{height:100%}.game{height:100%;display:flex;flex-direction:column;align-items:center;justify-content:space-evenly;flex-wrap:wrap;font-size:2vmin}.board-wrapper{position:relative;width:40em;height:40em}.board-wrapper.ended .board{pointer-events:none;opacity:0.4}.board-wrapper.ended .reset-block{opacity:1;transform:scale(1);pointer-events:auto}.board-wrapper .board{width:100%;height:100%;margin:0;flex-wrap:wrap;border-spacing:0;touch-action:none}.board-wrapper .reset-block{position:absolute;width:100%;height:100%;display:flex;justify-content:center;align-items:center;opacity:0;transform:scale(0.6);transition:transform .2s ease, opacity .2s ease;pointer-events:none;z-index:1}.board-wrapper .reset-block .win-caption{font-size:4em;text-shadow:1px -1px 4px rgba(0,0,0,0.3);font-weight:bold;-webkit-user-select:none;-ms-user-select:none;user-select:none}.cell{border:1px solid rgba(0,0,0,0.2);justify-content:center;padding:0;align-items:center;background-color:#fff;transition:transform .2s ease, box-shadow .2s ease;-webkit-user-select:none;-ms-user-select:none;user-select:none}.cell>img{display:block;pointer-events:none}.cell.victorious{position:relative;z-index:1;animation:bounce 2s infinite}.status-panel{padding:1em 0;display:flex;justify-content:space-between;width:100%;max-width:40em}.status-panel .panel-x-block,.status-panel .panel-o-block,.status-panel .info-content{display:flex;align-items:flex-end;font-size:2.6em}.status-panel .panel-x-block>img,.status-panel .panel-o-block>img,.status-panel .info-content>img{width:1.2em;transition:transform .2s ease, opacity .2s ease}.status-panel .panel-x-block,.status-panel .panel-o-block{font-family:'Roboto Mono', monospace}.status-panel .panel-x-block>img,.status-panel .panel-o-block>img{opacity:0.4;margin-left:0.2em;transform-origin:left center}.status-panel .panel-o-block>img{margin-left:0;margin-right:0.2em;transform-origin:right center}.status-panel.x-move .panel-x-block>img,.status-panel.o-move .panel-o-block>img{opacity:1;transform:scale(1.6)}.menu{height:100%;display:flex;flex-direction:column;align-items:center;justify-content:space-evenly;font-size:2vmin}.menu .menu-items{width:100%;padding:0 6em;flex-grow:1;display:flex;flex-direction:column;justify-content:center}.menu .menu-items .menu-item{width:100%;font-size:2em;font-family:inherit;font-weight:bold;padding:0.6em;border:1px solid rgba(0,0,0,0.2);background-color:transparent;transition:color .2s ease, background-color .2s ease;cursor:pointer}.menu .menu-items .menu-item:hover{background-color:rgba(63,81,181,0.8);color:#fff}.menu .game-configs{width:100%;padding:0 6em;flex-grow:2;display:flex;flex-direction:column;justify-content:space-evenly}.menu .game-configs .config-item{font-size:2em}.config-item .menu-tab{font-family:inherit;font-size:0.8em}.config-item .menu-tab .MuiTab-wrapper{width:auto;font-size:1em;display:flex;flex-direction:column;justify-content:space-between;height:100%}.config-item .menu-tab .MuiTab-wrapper .tab-icon{display:flex;flex-grow:1;width:100%}@media (hover: none){.cell:not(.victorious).hovered{position:relative;transform:scale(1.15);box-shadow:0 0 4px rgba(0,0,0,0.6);z-index:1}.cell:not(.hovered).highlighted{background-color:rgba(0,0,0,0.1)}}@media (hover: hover){.cell:not(.victorious):hover{position:relative;transform:scale(1.15);box-shadow:0 0 4px rgba(0,0,0,0.6);z-index:1}}@keyframes bounce{0%{transform:scale(1)}10%{transform:scale(1.2)}20%{transform:scale(1)}100%{transform:scale(1)}}

* {
  box-sizing: border-box;
}

html, body {
  width: 100%;
  height: 100%;
}

body {
  margin: 0;
  font-family: 'Comfortaa', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
